import React from "react";
import { Image } from "semantic-ui-react";
import "./style.css";

export const T = (props) => {
  return (
    <div
      style={{
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Image
        src={require("../../images/complainify.png")}
        size="small"
        className="class3"
      ></Image>
    </div>
  );
};
