import React, { Suspense, lazy } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import { T } from "../Fallback/T";
import "../../animation.css";
import Selector from "../TypeSelector/index";
const Notices = lazy(() => import("../Notices/Index"));
const About = lazy(() => import("../About/About"));
const Notes = lazy(() => import("../Notes/Notes"));
const AuthHeader = lazy(() => import("../authHeader/Header"));
const Chat = lazy(() => import("../Chat/chat"));
const Login = lazy(() => import("../Login/Login"));
const HomeNav = lazy(() => import("../HomeNavigator/homenav"));
const NotFound = lazy(() => import("../NotFound/Not"));
const Auth = lazy(() => import("../AuthenticateComponent/Auth"));
const NearByProblems = lazy(() => import("../NearBy/Problems/problems"));
const MyProfile = lazy(() => import("../Person/MyProfile/MyProfile"));
const Others = lazy(() => import("../Person/OthersProfile/OthersProfile"));
const Signup = lazy(() => import("../SignUp/signup"));
const NearByNav = lazy(() => import("../NearByNavigator/NearByNav"));
const SensitiveProblems = lazy(() => import("../NearBy/SensitiveProblem"));
const Games = lazy(() => import("../Games/index"));
const DefaultPage = lazy(() => import("../DefaultPage"));
const FPEmail = lazy(() => import("../ForgotPass/EnterEmail/EnterEmail"));
const PR = lazy(() => import("../ForgotPass/SettingThePassword/ResetPass"));
const MyProblems = lazy(() => import("../Home/MyProblems/MyProblems"));

const fallback = {
  fallback: <T></T>,
};
const AuthMyProblems = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={MyProblems}></Auth>
    </Suspense>
  );
};
const AuthNearByProblems = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={NearByProblems}></Auth>
    </Suspense>
  );
};
const AuthMyProfile = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={MyProfile}></Auth>
    </Suspense>
  );
};
const AuthOthers = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={Others}></Auth>
    </Suspense>
  );
};
const AuthSensitive = (props) => {
  return (
    <Suspense {...fallback}>
      {" "}
      <Auth {...props} Component={SensitiveProblems}></Auth>
    </Suspense>
  );
};
const AuthNotes = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={Notes}></Auth>
    </Suspense>
  );
};

const AuthMessages = (props) => {
  return (
    <Suspense {...fallback}>
      {" "}
      <Auth {...props} Component={Chat}></Auth>
    </Suspense>
  );
};
const AuthGames = (props) => {
  return (
    <Suspense {...fallback}>
      {" "}
      <Auth {...props} Component={Games}></Auth>
    </Suspense>
  );
};
const AuthNotices = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={Notices}></Auth>
    </Suspense>
  );
};
const AuthAbout = (props) => {
  return (
    <Suspense {...fallback}>
      <Auth {...props} Component={About}></Auth>
    </Suspense>
  );
};
const Router = (props) => {
  const AuthenticatedRoutes = (
    <div>
      <Suspense {...fallback}>
        {props.isAuth && (
          <Route path="/anonymity" component={AuthHeader}></Route>
        )}
        {props.isAuth && (
          <Route path="/anonymity/Home" component={HomeNav}></Route>
        )}
        {props.isAuth && (
          <Route path="/anonymity/nearby" component={NearByNav}></Route>
        )}

        <Switch>
          <Route exact path="/test" component={T}></Route>
          <Route exact path="/PassReset/:token" component={PR}></Route>
          <Route exact path="/forgetPass" component={FPEmail}></Route>
          <Route
            exact
            path="/anonymity/nearby/sensitive"
            component={AuthSensitive}
          ></Route>
          <Route
            exact
            path="/anonymity/Nearby"
            component={AuthNearByProblems}
          ></Route>

          <Route exact path="/signup" component={Signup}></Route>

          <Route exact path="/login" component={Login}></Route>
          <Route
            exact
            path="/anonymity/Home/MyProblems"
            component={AuthMyProblems}
          ></Route>
          <Route
            exact
            path="/anonymity/person"
            component={AuthMyProfile}
          ></Route>
          <Route
            exact
            path="/anonymity/person/:id"
            component={AuthOthers}
          ></Route>
          <Route
            exact
            path="/anonymity/Home"
            component={AuthMyProblems}
          ></Route>
          <Route
            exact
            path="/anonymity/Home/Notes"
            component={AuthNotes}
          ></Route>
          <Route exact path="/anonymity/About" component={AuthAbout}></Route>

          <Route
            exact
            path="/anonymity/messages"
            component={AuthMessages}
          ></Route>
          <Route path="/anonymity/ranking" component={AuthGames}></Route>
          <Route
            exact
            path="/anonymity/home/Notices"
            component={AuthNotices}
          ></Route>
          <Route exact path="/s">
            <Selector title="Sort By" value="Oldest"></Selector>
          </Route>
          <Route exact path="/" component={DefaultPage}></Route>
          <Route path="" component={NotFound}></Route>
        </Switch>
      </Suspense>
    </div>
  );

  return <BrowserRouter>{AuthenticatedRoutes}</BrowserRouter>;
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.authReducer,
  };
};

export default connect(mapStateToProps)(Router);
